import React, { Component } from "react";
import review from "../../requisicao/reviewReq";
import Chart from "react-apexcharts";

const vetApresentao = {
  a: 0,
  b: 0,
  c: 0,
  d: 0,
  e: 0,
};

review().then((Object) => {
  for (let i = 0; i < Object.length; i++) {
    if (Object[i].apresentacao == 1) {
      vetApresentao.a++;
    }
    if (Object[i].apresentacao == 2) {
      vetApresentao.b++;
    }
    if (Object[i].apresentacao == 3) {
      vetApresentao.c++;
    }
    if (Object[i].apresentacao == 4) {
      vetApresentao.d++;
    }
    if (Object[i].apresentacao == 5) {
      vetApresentao.e++;
    }
  }
});

class GraficoApresentacao extends Component {
  constructor(props) {
    super(props);

    this.state = {
      options: {
        chart: {
          id: "basic-bar",
        },
        xaxis: {
          categories: [1, 2, 3, 4, 5],
        },
      },
      series: [
        {
          name: "Avaliaes",
          data: [
            vetApresentao.a,
            vetApresentao.b,
            vetApresentao.c,
            vetApresentao.d,
            vetApresentao.e,
          ],
        },
      ],
    };
  }

  render() {
    return (
      <div className="app">
        <div className="row">
          <div className="mixed-chart">
            <Chart
              options={this.state.options}
              series={this.state.series}
              type="bar"
            />
          </div>
        </div>
      </div>
    );
  }
}
export default GraficoApresentacao;
