import "../assets/styles/login.scss";
import {
  Form,
  Button,
  Modal,
  Container,
  Spinner,
  Row,
  Figure,
  Alert,
  Col,
  OverlayTrigger,
  Popover,
  Stack,
} from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { checkEmailUniqueness } from "../service/EmailService";
import { useMediaQuery } from "react-responsive";
const popover = (
  <Popover id="popover-basic" className="transition-effect">
    <Popover.Header as="h3">Padro de senha</Popover.Header>
    <Popover.Body>
      <p>
        A senha deve conter entre 8-100 caracteres, com ao menos uma letra
        maiscula, minscula e um smbolo. No pode conter espaos ou emojis.
      </p>
    </Popover.Body>
  </Popover>
);

const logo = require("../assets/images/logo.png");
const passwordValidator = require("password-validator");
const schema = new passwordValidator();
schema
  .is()
  .min(8) // Minimum length 8
  .is()
  .max(100) // Maximum length 100
  .has()
  .uppercase() // Must have uppercase letters
  .has()
  .lowercase() // Must have lowercase letters
  .has()
  .digits(2) // Must have at least 2 digits
  .has()
  .not()
  .spaces() // Should not have spaces
  .is()
  .not()
  .oneOf(["Passw0rd", "Password123"]); // Blacklist these values

// Aqui comea o cdigo do signup

const SignUpConst = () => {
  const isResponsive = useMediaQuery({ query: "(max-width: 768px)" });
  // Criando constante navigate
  const id = localStorage.getItem("id");
  useEffect(() => {
    if (id == 1) {
      navigate("/dashboard");
    }
  }, [id]);
  const navigate = useNavigate();

  // Setando constantes e funces

  const [nome, setNome] = useState("");
  const [email, setEmail] = useState("");
  const [senha, setSenha] = useState("");
  const [confirmarSenha, setConfirmarSenha] = useState("");
  const [termo, setTermo] = useState(false);

  // Modais

  const [spinnerModal, setSpinnerModal] = useState(false);
  const [formModal, setFormModal] = useState(false);
  const [termoModal, setTermoModal] = useState(false);

  // Alerts

  const [showTermoAlert, setTermoAlert] = useState(false);
  const [showPassAlert, setShowPassAlert] = useState(false);
  const [showAlertSuccess, setShowAlertSuccess] = useState(false);
  const [showP, setCampoP] = useState(false);
  const [showErrorAlert, setErrorAlert] = useState(false);
  const [showCampoAlert, setCampoAlert] = useState(false);
  const [showWrongPAlert, setWrongPAlert] = useState(false);
  const [showEmailAlert, setEmailAlert] = useState(false);
  const [showColetaDeDadosAlert, setColetaDeDados] = useState(false);

  // Coletas de usurio

  const [coletaPreenchida, setColetaPreenchida] = useState(false);
  const [dadosColeta, setDadosColeta] = useState("");
  let [vegetariano, setVegetariano] = useState(null);
  const [vinculoAoIfes, setVinculoAoIfes] = useState(null);
  let [refeicoes, setRefeicoes] = useState([]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    // *** validacao de campo vazio ***
    if (senha === "" || email === "" || nome === "") {
      setCampoAlert(true);
      setTimeout(() => {
        setCampoAlert(false);
      }, 5000);
      return;
    }
    // *** validacao de senha ***
    if (schema.validate(senha) === false) {
      setCampoP(true);
      setWrongPAlert(true);
      setTimeout(() => {
        setWrongPAlert(false);
      }, 5000);
      setTimeout(() => {
        setCampoP(false);
      }, 10000);
      return;
    }
    // *** validacao de confirmar senha ***
    if (senha !== confirmarSenha) {
      setShowPassAlert(true);
      setTimeout(() => {
        setShowPassAlert(false);
      }, 5000);
      return;
    }
    // *** validacao de termo preenchido ***
    if (termo === false) {
      setTermoAlert(true);
      setTimeout(() => {
        setTermoAlert(false);
      }, 5000);
      return;
    }

    const isEmailUnique = await checkEmailUniqueness(email);
    if (isEmailUnique === false) {
      setEmailAlert(true);
      setTimeout(() => {
        setEmailAlert(false);
      }, 5000);
    } else {
      setColetaPreenchida(true);
      setFormModal(true);
    }
  };
  // ****** ao clicar no botao exibe o modal de coleta de dados ******
  const handleColetaSubmit = async () => {
    let objetoRefeicoes = {
      cafeDaManha: false,
      almoco: false,
      lancheDaTarde: false,
      jantar: false,
    };
    for (const element of refeicoes) {
      if (element == 1) {
        objetoRefeicoes.cafeDaManha = true;
      } else if (element == 2) {
        objetoRefeicoes.almoco = true;
      } else if (element == 3) {
        objetoRefeicoes.lancheDaTarde = true;
      } else if (element == 4) {
        objetoRefeicoes.jantar = true;
      }
    }

    let verify = false;

    if (vinculoAoIfes == null) {
      verify = true;
    } else if (
      objetoRefeicoes.cafeDaManha != true &&
      objetoRefeicoes.almoco != true &&
      objetoRefeicoes.lancheDaTarde != true &&
      objetoRefeicoes.jantar != true
    ) {
      verify = true;
    } else if (vegetariano == null) {
      verify = true;
    }

    if (verify == true) {
      objetoRefeicoes = {
        cafeDaManha: false,
        almoco: false,
        lancheDaTarde: false,
        jantar: false,
      };
      verify = false;
    } else {
      if (vegetariano === "true") {
        vegetariano = true;
      }
      if (vegetariano === "false") {
        vegetariano = false;
      }
      const dadosCompletos = {
        nome,
        email,
        senha,
        vinculoAoIfes,
        objetoRefeicoes,
        vegetariano,
      };

      try {
        const response = await fetch(`http://localhost:9000/signup`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },

          body: JSON.stringify(dadosCompletos),
        });

        if (response.ok) {
          setShowAlertSuccess(true);
          setTimeout(() => {
            setShowAlertSuccess(false);
          }, 5000);
          setTimeout(() => {
            setSpinnerModal(true);
          }, 1000);
          setTimeout(() => {
            navigate("/login");
          }, 4000);
        } else {
          setErrorAlert(true);
          setTimeout(() => {
            setErrorAlert(false);
          }, 5000);
        }
      } catch (error) {
        setErrorAlert(true);
        setTimeout(() => {
          setErrorAlert(false);
        }, 5000);
      }
    }
  };

  const handleVinculoChange = (e) => {
    setVinculoAoIfes(e.target.value);
  };

  const handleVegetarianoChange = (e) => {
    setVegetariano(e.target.value);
  };

  const handleRefeicoes = (e) => {
    const value = e.target.value;
    if (e.target.checked) {
      setRefeicoes([...refeicoes, value]);
    } else {
      setRefeicoes(refeicoes.filter((refeicao) => refeicao !== value));
    }
  };

  return (
    <>
      <Row className="position-fixed alert-row" style={{ marginTop: 100 }}>
        {showPassAlert && (
          <Alert
            variant="warning"
            className="align-items-center d-flex fade"
            onClose={() => setShowPassAlert(false)}
          >
            As senhas devem ser iguais
          </Alert>
        )}{" "}
        {showAlertSuccess && (
          <Alert
            variant="success"
            className="align-items-center d-flex fade"
            onClose={() => setShowAlertSuccess(false)}
          >
            Cadastro concludo
          </Alert>
        )}
        {showCampoAlert && (
          <Alert
            variant="warning"
            className="align-items-center d-flex fade"
            onClose={() => setCampoAlert(false)}
          >
            Todos os campos devem ser preenchidos
          </Alert>
        )}
        {showErrorAlert && (
          <Alert
            variant="danger"
            className="align-items-center d-flex fade"
            onClose={() => setErrorAlert(false)}
          >
            Erro ao enviar requisio
          </Alert>
        )}
        {showEmailAlert && (
          <Alert
            variant="danger"
            className="align-items-center d-flex fade"
            onClose={() => setEmailAlert(false)}
          >
            Este email j foi cadastrado
          </Alert>
        )}
        {showWrongPAlert && (
          <Alert
            variant="warning"
            className="align-items-center d-flex fade"
            onClose={() => setWrongPAlert(false)}
          >
            A senha digitada no est de acordo com as normas
          </Alert>
        )}
        {showTermoAlert && (
          <Alert
            variant="warning"
            className="align-items-center d-flex fade"
            onClose={() => setTermoAlert(false)}
          >
            Leia e concorde com os Termos de Uso
          </Alert>
        )}
        {showColetaDeDadosAlert && (
          <Alert
            variant="warning"
            className="align-items-center d-flex fade"
            onClose={() => setTermoAlert(false)}
          >
            Dado no inserido
          </Alert>
        )}
      </Row>

      {/* Modal spinner */}

      <Modal
        show={spinnerModal}
        onHide={() => setSpinnerModal(false)}
        className="modal spinner-modal"
        backdrop="static"
        data-test="links"
      >
        <Modal.Body>
          <Spinner
            animation="border"
            role="status"
            show={spinnerModal}
            onHide={() => setSpinnerModal(false)}
          >
            <span className="visually-hidden">Loading...</span>
          </Spinner>
        </Modal.Body>
      </Modal>

      {/* Modal termo de uso */}

      <Modal
        show={termoModal}
        onHide={() => setTermoModal(false)}
        className="modal coleta-modal"
        backdrop="static"
        data-test="links"
      >
        <Modal.Header>
          <Modal.Title>
            <h2>TERMOS DE USO</h2>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body className="overflow-scroll p-5">
          <Container className="h-100">
            <Row>
              <Col>
                <h3>1. Aceitao dos Termos de Uso</h3>
                <p>
                  Bem-vindo ao Nutrifood! Ao acessar ou usar nosso site, voc
                  concorda em cumprir e ficar vinculado a estes Termos de Uso.
                  Se voc no concordar com algum destes termos, por favor, no
                  utilize o nosso site.
                </p>

                <h3>2. Uso Autorizado</h3>
                <p>
                  Voc concorda em utilizar nosso site apenas para fins legais e
                  de acordo com estes Termos de Uso. Voc no deve usar nosso
                  site de forma que possa causar danos, prejudicar ou interferir
                  com o funcionamento normal do site.
                </p>

                <h3>3. Informaes de Conta</h3>
                <p>
                  Ao utilizar nosso site, voc  responsvel por manter a
                  confidencialidade de suas informaes de conta, incluindo nome
                  de usurio e senha. Voc concorda em nos notificar
                  imediatamente sobre qualquer uso no autorizado de sua conta.
                </p>

                <h3>4. Direitos de Propriedade Intelectual</h3>
                <p>
                  Nosso site e seu contedo, incluindo texto, grficos,
                  logotipos, imagens e software, so protegidos por leis de
                  direitos autorais e outras leis de propriedade intelectual.
                  Voc no deve reproduzir, distribuir, modificar ou criar obras
                  derivadas com base em nosso contedo, a menos que tenha
                  permisso expressa.
                </p>

                <h3>5. Links para Outros Sites</h3>
                <p>
                  Nosso site pode conter links para sites de terceiros que no
                  controlamos. No somos responsveis pelo contedo ou pelas
                  prticas de privacidade desses sites.
                </p>

                <h3>6. Iseno de Garantias</h3>
                <p>
                  Nosso site  fornecido "como est" e "conforme disponvel".
                  No fazemos garantias de qualquer tipo quanto  sua preciso,
                  confiabilidade ou disponibilidade.
                </p>

                <h3>7. Limitao de Responsabilidade</h3>
                <p>
                  Em nenhuma circunstncia seremos responsveis por danos
                  diretos, indiretos, incidentais, especiais, consequentes ou
                  punitivos decorrentes do uso ou incapacidade de uso do nosso
                  site.
                </p>

                <h3>8. Modificaes nos Termos de Uso</h3>
                <p>
                  Reservamo-nos o direito de modificar estes Termos de Uso a
                  qualquer momento, e  sua responsabilidade verificar
                  periodicamente se houve alteraes. O uso continuado do site
                  aps tais alteraes constituir sua aceitao dos Termos de
                  Uso revisados.
                </p>

                <h3>9. Encerramento de Conta</h3>
                <p>
                  Reservamo-nos o direito de encerrar ou suspender sua conta a
                  qualquer momento, sem aviso prvio, por qualquer motivo.
                </p>

                <h3>10. Lei Aplicvel</h3>
                <p>
                  Estes Termos de Uso sero regidos e interpretados de acordo
                  com as leis do [seu pas/estado], sem levar em considerao
                  suas disposies de conflito de leis.
                </p>
              </Col>
            </Row>
          </Container>
        </Modal.Body>
        <Modal.Footer>
          <Button
            className="bt-sub"
            onClick={() => {
              setTermoModal(false);
            }}
          >
            Fechar
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Modal de coleta de usurio */}

      <Modal
        show={formModal}
        onHide={() => setFormModal(false)}
        className="modal coleta-modal"
        backdrop="static"
        data-test="links"
      >
        <Modal.Header>
          <Modal.Title>Coleta de usurio</Modal.Title>
        </Modal.Header>
        <Modal.Body className="overflow-scroll">
          <Container className="h-100">
            <Row>
              <Col md={12}>
                <Form>
                  <Row>
                    <Col md={12}>
                      {["radio"].map((type) => (
                        <Row
                          className="row-coleta mb-4 bg-light"
                          key={`inline-${type}`}
                        >
                          <Col md={12} xs={12} className="text-start mb-3">
                            <h5>
                              Qual seu vnculo com o IFES - Campus Santa Teresa?{" "}
                            </h5>
                          </Col>
                          <Col xs={2} className="d-flex justify-content-end">
                            <Form.Check
                              inline
                              name="vinculoAoIfes"
                              type={type}
                              id={`inline-${type}-1`}
                              value={"AM"}
                              checked={vinculoAoIfes === "AM"}
                              onChange={handleVinculoChange}
                            />
                          </Col>
                          <Col xs={10} className="d-flex justify-content-start">
                            <Form.Label>Aluno do ensino mdio</Form.Label>
                          </Col>

                          <Col xs={2} className="d-flex justify-content-end">
                            {" "}
                            <Form.Check
                              inline
                              name="vinculoAoIfes"
                              type={type}
                              id={`inline-${type}-2`}
                              value={"AG"}
                              checked={vinculoAoIfes === "AG"}
                              onChange={handleVinculoChange}
                            />
                          </Col>
                          <Col xs={10} className="d-flex justify-content-start">
                            <Form.Label>Aluno da graduao</Form.Label>
                          </Col>

                          <Col xs={2} className="d-flex justify-content-end">
                            <Form.Check
                              inline
                              name="vinculoAoIfes"
                              type={type}
                              id={`inline-${type}-3`}
                              value={"SDT"}
                              checked={vinculoAoIfes === "SDT"}
                              onChange={handleVinculoChange}
                            />
                          </Col>
                          <Col xs={10} className="d-flex justify-content-start">
                            <Form.Label>
                              Servidor, docente ou tercerizado
                            </Form.Label>
                          </Col>

                          <Col xs={2} className="d-flex justify-content-end">
                            <Form.Check
                              inline
                              name="vinculoAoIfes"
                              type={type}
                              id={`inline-${type}-4`}
                              value={"Outro"}
                              checked={vinculoAoIfes === "Outro"}
                              onChange={handleVinculoChange}
                            />
                          </Col>
                          <Col xs={10} className="d-flex justify-content-start">
                            <Form.Label>Outro</Form.Label>
                          </Col>
                        </Row>
                      ))}
                    </Col>
                  </Row>
                </Form>
              </Col>
              <Col md={12}>
                <Form>
                  <Row>
                    <Col md={12}>
                      {["checkbox"].map((type) => (
                        <Row
                          className="mb-4 row-coleta bg-light"
                          key={`inline-${type}`}
                        >
                          <Col md={12} xs={12} className="text-start mb-3">
                            <h5>
                              Qual refeio voc realiza no restaurante
                              institucional?
                            </h5>
                          </Col>

                          <Col xs={2} className="d-flex justify-content-end">
                            <Form.Check
                              inline
                              name="refeicoes"
                              type={type}
                              id={`inline-${type}-4`}
                              value={1}
                              onChange={handleRefeicoes}
                            />
                          </Col>
                          <Col xs={10} className="d-flex justify-content-start">
                            <Form.Label>Caf da manh</Form.Label>
                          </Col>
                          <Col xs={2} className="d-flex justify-content-end">
                            <Form.Check
                              inline
                              name="refeicoes"
                              type={type}
                              id={`inline-${type}-5`}
                              value={2}
                              onChange={handleRefeicoes}
                            />
                          </Col>
                          <Col xs={10} className="d-flex justify-content-start">
                            <Form.Label>Almoo</Form.Label>
                          </Col>
                          <Col xs={2} className="d-flex justify-content-end">
                            <Form.Check
                              inline
                              name="refeicoes"
                              type={type}
                              id={`inline-${type}-6`}
                              value={3}
                              onChange={handleRefeicoes}
                            />
                          </Col>
                          <Col xs={10} className="d-flex justify-content-start">
                            <Form.Label>Lanche da tarde</Form.Label>
                          </Col>
                          <Col xs={2} className="d-flex justify-content-end">
                            <Form.Check
                              inline
                              name="refeicoes"
                              type={type}
                              id={`inline-${type}-7`}
                              value={4}
                              onChange={handleRefeicoes}
                            />
                          </Col>
                          <Col xs={10} className="d-flex justify-content-start">
                            <Form.Label>Jantar</Form.Label>
                          </Col>
                        </Row>
                      ))}
                    </Col>
                  </Row>
                </Form>
              </Col>
              <Col md={12}>
                <Form>
                  <Row>
                    <Col md={12}>
                      {["radio"].map((type) => (
                        <Row
                          className="mb-4 row-coleta bg-light"
                          key={`inline-${type}`}
                        >
                          <Col md={12} xs={12} className="text-start mb-3">
                            <h5>Voc  vegetariano?</h5>
                          </Col>
                          <Col md={5} xs={5}>
                            <Stack gap={5} className="d-flex flex-row">
                              <Stack gap={2} className="d-flex flex-row">
                                <Form.Check
                                  xs={12}
                                  inline
                                  name="vegetariano"
                                  type={type}
                                  id={`inline-${type}-9`}
                                  value={true}
                                  checked={vegetariano === "true"}
                                  onChange={handleVegetarianoChange}
                                />
                                <Form.Label>Sim</Form.Label>
                              </Stack>
                              <Stack gap={2} className="d-flex flex-row">
                                <Form.Check
                                  inline
                                  name="vegetariano"
                                  type={type}
                                  id={`inline-${type}-10`}
                                  value={false}
                                  checked={vegetariano === "false"}
                                  onChange={handleVegetarianoChange}
                                />
                                <Form.Label>No</Form.Label>
                              </Stack>
                            </Stack>
                          </Col>
                        </Row>
                      ))}
                    </Col>
                  </Row>
                </Form>
              </Col>
            </Row>
          </Container>
        </Modal.Body>
        <Modal.Footer className="w-100">
          <Row className="w-100 row-bt" xs={2}>
            <Col md={6} className="o-enviar p-1 order-1">
              <Button
                variant="primary"
                className="w-100 bt-sub"
                onClick={() => {
                  handleColetaSubmit();
                  setFormModal(false);
                }}
              >
                Enviar
              </Button>
            </Col>{" "}
            <Col md={6} className="o-cancelar p-1 order-0">
              <Link to={"/sign-up"} reloadDocument>
                <Button className="w-100 bg-light border-danger text-danger cancelar">
                  Cancelar
                </Button>
              </Link>
            </Col>
          </Row>
        </Modal.Footer>
      </Modal>

      {/* Aqui comea o container de cadastro */}

      <div id="div-z-cont" data-test="links">
        <Container className="login-cont">
          <section>
            <Row>
              {/* Formulario de cadastro */}

              <Form id="form-login" onSubmit={handleSubmit}>
                <div id="div-form-l">
                  <Figure className="logo-tog">
                    <Figure.Image src={logo} />
                  </Figure>
                  <h1>Cadastro</h1>

                  {/* <div className="in-section">
                    <Form.Group className="mb-3">
                      <Form.Label>Insira seus dados pessoais abaixo</Form.Label>
                    </Form.Group>
                  </div> */}

                  <div className="in-section">
                    <Form.Group className="mb-3">
                      <Form.Control
                        type="text"
                        aria-label="Nome"
                        placeholder="Nome"
                        autoCapitalize="Nome"
                        value={nome}
                        data-test="form-nome"
                        onChange={(e) => setNome(e.target.value)}
                      />
                      <Form.Control
                        type="email"
                        autoComplete="email"
                        placeholder="Email"
                        value={email}
                        data-test="form-email"
                        onChange={(e) => setEmail(e.target.value)}
                      />

                      <Form.Control
                        type="password"
                        placeholder="Senha"
                        value={senha}
                        data-test="form-pass"
                        onChange={(e) => setSenha(e.target.value)}
                      />

                      <Form.Control
                        type="password"
                        placeholder="Confirmar senha"
                        data-test="form-passc"
                        value={confirmarSenha}
                        onChange={(e) => setConfirmarSenha(e.target.value)}
                      />
                      {showP &&
                        (isResponsive ? (
                          <OverlayTrigger
                            trigger="click"
                            placement="top"
                            overlay={popover}
                            className="transition-effect overlay-t"
                          >
                            <Link className="text-decoration-none transition-effect">
                              Conferir padro de senha
                            </Link>
                          </OverlayTrigger>
                        ) : (
                          <OverlayTrigger
                            trigger="click"
                            placement="right"
                            overlay={popover}
                            className="transition-effect overlay-t"
                          >
                            <Link className="text-decoration-none transition-effect">
                              Conferir padro de senha
                            </Link>
                          </OverlayTrigger>
                        ))}
                    </Form.Group>
                  </div>
                  <div className="d-flex justify-content-left align-items-center flex-row mb-1">
                    <Form.Group
                      id="termosDeUso"
                      className="d-flex flex-row align-items-center justify-content-center"
                    >
                      <Form.Check
                        type="checkbox"
                        className="t-uso"
                        value={termo}
                        onChange={(e) => setTermo(e.target.value)}
                      />
                      <Form.Label htmlFor="termosDeUso" className="m-0">
                        Concordo com os{" "}
                        <Link
                          onClick={() => {
                            setTermoModal(true);
                          }}
                        >
                          Termos de Uso
                        </Link>
                      </Form.Label>
                    </Form.Group>
                  </div>

                  <div id="div-btn">
                    <Stack gap={2} as="row" className="w-100 d-flex flex-row">
                      <Col>
                        <Link to={"/login"}>Fazer login</Link>
                      </Col>
                      <Col>
                        <Button
                          type="submit"
                          id="button-login-signup"
                          className="bt-sub w-100"
                          data-test="cadastrar"
                        >
                          Cadastrar
                        </Button>
                      </Col>
                    </Stack>
                  </div>
                </div>
              </Form>

              {/* *************************** */}
            </Row>
          </section>
        </Container>
      </div>
    </>
  );
};

export default SignUpConst;
