import {
  Button,
  Row,
  Container,
  Col,
  ListGroup,
  ListGroupItem,
  Figure,
  Form,
  Card,
  Offcanvas,
  Badge,
  Modal,
  Stack,
  Alert,
} from "react-bootstrap";
import { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useMediaQuery } from "react-responsive";
import { format } from "date-fns";
import palavraProfano from "../service/Profano";
import Filter from "bad-words";
const SettingsConst = () => {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const navigate = useNavigate();
  const id = localStorage.getItem("id");
  const [suggestions, setSuggestions] = useState([]);
  const [idSug, setIdSug] = useState([]);
  const [dataCriacao, setDataCriacao] = useState([]);
  const [edit, setEdit] = useState(false);
  const [isDelete, setIsDelete] = useState(false);
  const [selecionaSugestaoIndex, setSelecionaSugestaoIndex] = useState(null);
  const [showAlertProfano, setShowAlertProfano] = useState(false);
  const [showAlertErro, setShowAlertErro] = useState(false);
  const [salvar, setSalvar] = useState("");
  const [conteudo, setConteudo] = useState("");
  const [idSuggestions, setIdSuggestions] = useState("");

  const [characterLimit] = useState(200);
  useEffect(() => {
    if (id == 1) {
      navigate("/dashboard");
    } else if (id == null) {
      navigate("/login");
    } else {
      const fetchData = async () => {
        try {
          const response = await fetch("http://localhost:9000/getSuggestions", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({ id }),
          });

          if (response.ok) {
            const data = await response.json();
            if (data.message === "OK") {
              setSuggestions(data.suggestions);
              setIdSug(data.id);
              setDataCriacao(data.data_criacao);
            } else {
              setShowAlertErro(true);
              setTimeout(() => {
                setShowAlertErro(false);
              }, 5000);
            }
          } else {
            setShowAlertErro(true);
            setTimeout(() => {
              setShowAlertErro(false);
            }, 5000);
          }
        } catch (error) {
          setShowAlertErro(true);
          setTimeout(() => {
            setShowAlertErro(false);
          }, 5000);
        }
      };

      fetchData();
    }
  }, [id]);

  const handleEdit = (index) => {
    setSelecionaSugestaoIndex(index);
    setEdit(true);
    setConteudo(suggestions[index]);
    setIdSuggestions(idSug[index]);
  };

  const handleDelete = (index) => {
    setSelecionaSugestaoIndex(index);
    setIdSuggestions(idSug[index]);
    setIsDelete(true);
  };

  const handleConfirmDelete = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch("http://localhost:9000/deleteSuggestions", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ id, idSuggestions }),
      });

      if (response.ok) {
        const data = await response.json();
        if (data.message === "DELETADO") {
          window.location.reload();
        } else {
          setShowAlertErro(true);
          setTimeout(() => {
            setShowAlertErro(false);
          }, 5000);
        }
      } else {
        setShowAlertErro(true);
        setTimeout(() => {
          setShowAlertErro(false);
        }, 5000);
      }
    } catch (error) {
      setShowAlertErro(true);
      setTimeout(() => {
        setShowAlertErro(false);
      }, 5000);
    }
    setIsDelete(false);
  };

  const handleSalvar = async (e) => {
    if (salvar == "") {
      setEdit(false);
      return;
    }
    e.preventDefault();

    const filter = new Filter();
    filter.addWords(...palavraProfano);
    if (filter.isProfane(salvar)) {
      setShowAlertProfano(true);
      setTimeout(() => {
        setShowAlertProfano(false);
      }, 5000);
    } else {
      try {
        const response = await fetch("http://localhost:9000/editSuggestions", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ salvar, id, idSuggestions, conteudo }),
        });

        if (response.ok) {
          const data = await response.json();
          if (data.message === "SALVO") {
            window.location.reload();
          } else {
            setShowAlertErro(true);
            setTimeout(() => {
              setShowAlertErro(false);
            }, 5000);
          }
        } else {
          setShowAlertErro(true);
          setTimeout(() => {
            setShowAlertErro(false);
          }, 5000);
        }
      } catch (error) {
        setShowAlertErro(true);
        setTimeout(() => {
          setShowAlertErro(false);
        }, 5000);
      }
    }

    setEdit(false);
  };

  const isResponsive = useMediaQuery({ query: "(max-width: 768px)" });
  const arrow = require("../assets/images/left-arrow.png");

  return (
    <>
      <Row className="position-fixed alert-row rev-cont">
        {showAlertProfano && (
          <Alert
            variant="warning"
            className="align-items-center d-flex fade"
            onClose={() => setShowAlertProfano(false)}
          >
            Linguagem imprpria detectada
          </Alert>
        )}
        {showAlertErro && (
          <Alert
            variant="danger"
            className="align-items-center d-flex fade"
            onClose={() => setShowAlertErro(false)}
          >
            Erro
          </Alert>
        )}{" "}
      </Row>

      <Modal
        show={isDelete}
        onHide={() => setIsDelete(false)}
        className="modal modal-senha"
        backdrop="static"
        data-test="links"
      >
        <Modal.Body>
          Voc tem certeza que quer excluir esta sugesto?
          <Row className="p-2">
            <Col>
              <Stack gap={2} className="d-flex flex-row">
                <Button
                  className="w-50 bt-sub-cancel"
                  type="click"
                  onClick={() => {
                    setIsDelete(false);
                  }}
                >
                  No
                </Button>
                <Button
                  className="w-50 bt-sub"
                  type="click"
                  onClick={handleConfirmDelete}
                >
                  Sim
                </Button>
              </Stack>
            </Col>
          </Row>
        </Modal.Body>
      </Modal>
      <div className="bg-light div-config d-flex justify-content-center align-items-center">
        <Container className="cont-resp">
          <Row className="w-100 overflow-hidden pb-0 d-flex justify-content-center row-response">
            {isResponsive ? (
              <div className="tab-response p-0">
                {" "}
                <Offcanvas show={show} onHide={handleClose}>
                  <Offcanvas.Header closeButton>
                    <Offcanvas.Title>Configuraes</Offcanvas.Title>
                  </Offcanvas.Header>
                  <Offcanvas.Body>
                    <ListGroup className="mb-3 tab-resp d-flex p-0 pt-1">
                      <ListGroupItem>
                        <Link to="/user/settings">
                          <Button>Conta do Usurio</Button>
                        </Link>
                      </ListGroupItem>
                      <ListGroupItem>
                        <Link to="/user/settings-security">
                          <Button>Segurana e Privacidade</Button>
                        </Link>
                      </ListGroupItem>
                      <ListGroupItem>
                        <Link to="/user/settings-data">
                          <Button>Coleta de Dados</Button>
                        </Link>
                      </ListGroupItem>
                    </ListGroup>
                  </Offcanvas.Body>
                </Offcanvas>
                <Row>
                  <Col sm={8} xs={8} className="d-flex align-items-center mt-3">
                    <Link
                      to="/"
                      className="text-decoration-none back d-flex align-items-center"
                    >
                      <Figure className="d-flex align-items-center m-0 p-0">
                        <Figure.Image width={25} height={25} src={arrow} />{" "}
                        <Figure.Caption></Figure.Caption>
                      </Figure>
                      Voltar  pgina principal{" "}
                    </Link>
                  </Col>
                  <Col
                    sm={4}
                    xs={4}
                    className="off-canv d-flex align-items-center mt-3 justify-content-end"
                  >
                    <Button variant="primary" onClick={handleShow}>
                      <ion-icon name="menu"></ion-icon>
                    </Button>
                  </Col>
                </Row>
              </div>
            ) : (
              <Col lg={4} xs={4}>
                <div>
                  <Link to="/" className="text-decoration-none back">
                    <Figure>
                      <Figure.Image width={25} height={25} src={arrow} />{" "}
                      <Figure.Caption></Figure.Caption>
                    </Figure>
                    Voltar  pgina principal{" "}
                  </Link>
                </div>
                <ListGroup>
                  <ListGroupItem>
                    <Link to="/user/settings">
                      <Button className="bg-transparent border-0 text-black w-100">
                        <Row className="d-flex align-items-center">
                          <Col
                            md={4}
                            xs={4}
                            className="d-flex justify-content-center"
                          >
                            <ion-icon name="person-sharp"></ion-icon>
                          </Col>
                          <Col
                            md={8}
                            xs={8}
                            className="d-flex justify-content-center"
                          >
                            Conta do Usurio
                          </Col>
                        </Row>
                      </Button>
                    </Link>
                  </ListGroupItem>
                  <ListGroupItem>
                    <Link to="/user/settings-security">
                      <Button className="bg-transparent border-0 text-black w-100">
                        <Row className="d-flex align-items-center">
                          <Col
                            md={4}
                            xs={4}
                            className="d-flex justify-content-center"
                          >
                            <ion-icon name="lock-open-sharp"></ion-icon>
                          </Col>
                          <Col
                            md={8}
                            xs={8}
                            className="d-flex justify-content-center"
                          >
                            Segurana e Privacidade
                          </Col>
                        </Row>
                      </Button>
                    </Link>
                  </ListGroupItem>
                  <ListGroupItem>
                    <Link to="/user/settings-data">
                      <Button className="bg-transparent border-0 text-black w-100">
                        <Row className="d-flex align-items-center">
                          <Col
                            md={4}
                            xs={4}
                            className="d-flex justify-content-center"
                          >
                            <ion-icon name="server-sharp"></ion-icon>
                          </Col>
                          <Col
                            md={8}
                            xs={8}
                            className="d-flex justify-content-center"
                          >
                            Coleta de Dados
                          </Col>
                        </Row>
                      </Button>
                    </Link>
                  </ListGroupItem>
                </ListGroup>
              </Col>
            )}

            <Col
              lg={8}
              xs={12}
              md={8}
              className="cont-config2 overflow-scroll border bg-white"
            >
              <div className="">
                <div className="div-pattern">
                  <h2>Configuraes de Dados</h2>
                  <p>
                    Bem-vindo  seo de configuraes de dados. Aqui voc pode
                    gerenciar como os dados so coletados, armazenados e
                    utilizados em sua conta.
                  </p>
                  <h3>Histrico de Sugestes</h3>
                  <Row className="suges-row pt-3 pb-3">
                    <Col className="overflow-scroll h-100 suges-col p-2 pt-0">
                      <ListGroup className="m-0 p-0">
                        {suggestions.map((suggestion, index) => {
                          const dataFormatada = format(
                            new Date(dataCriacao[index]),
                            "dd/MM/yyyy"
                          );
                          return (
                            <Card key={index} className="m-2">
                              <Card.Title className="p-0 mb-1 m-0">
                                <Row className="p-2">
                                  <Col>
                                    Sugesto <span>#{index + 1}</span>
                                    {idSuggestions[index]}
                                  </Col>

                                  <Col className="align-items-center justify-content-end d-flex lixeira-lapis gap-2">
                                    <Button
                                      className="align-items-center justify-content-center btn-pencil"
                                      onClick={() => handleEdit(index)}
                                    >
                                      <ion-icon name="pencil"></ion-icon>
                                    </Button>
                                    <Button
                                      className="align-items-center justify-content-center btn-trash"
                                      onClick={() => handleDelete(index)}
                                    >
                                      <ion-icon name="trash"></ion-icon>
                                    </Button>
                                  </Col>
                                </Row>
                              </Card.Title>

                              <Card.Subtitle className="p-2 pt-0 pb-0 text-muted align-self-start">
                                {dataFormatada}
                              </Card.Subtitle>
                              <Card.Body className="pt-0 pb-0 p-2 m-0">
                                {edit && selecionaSugestaoIndex === index ? (
                                  <Form
                                    onSubmit={handleSalvar}
                                    className="formulario-card"
                                  >
                                    <Form.Control
                                      as="textarea"
                                      defaultValue={suggestion}
                                      style={{ height: "100px" }}
                                      value={salvar}
                                      maxLength={200}
                                      isInvalid={salvar.length > characterLimit}
                                      onChange={(e) =>
                                        setSalvar(e.target.value)
                                      }
                                    />
                                    <Row className="p-2">
                                      <Col>
                                        <Badge
                                          className="mt-3"
                                          bg={`${
                                            salvar.length > characterLimit
                                              ? "danger"
                                              : "secondary"
                                          }`}
                                        >
                                          {salvar.length}/{characterLimit}
                                        </Badge>
                                      </Col>
                                      <Col className="d-flex align-items-center justify-content-end">
                                        <Button
                                          type="submit"
                                          className="h-100 bt-sub"
                                        >
                                          Salvar
                                        </Button>
                                      </Col>
                                    </Row>
                                  </Form>
                                ) : (
                                  <span>{suggestion}</span>
                                )}
                              </Card.Body>
                            </Card>
                          );
                        })}
                      </ListGroup>
                    </Col>
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </>
  );
};

export default SettingsConst;
